/* 기본 스타일 */
body {
  font-family: Arial, Helvetica, sans-serif, serif;
  
  background-color: #f0f0f0;
  font-weight: 600;

  
}
tr{
  border: rgb(175, 175, 175) ;
}
.start {
  opacity: 0;
}
.end {
  opacity: 1;
  transition: opacity 0.7s;
}

.main-bg {
  height: 500px;
  background-image: url('./image/bg.png');
  background-size: cover;
  background-position: center;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  
  


 
  
  
}
#container {
  padding-top: 20px;

}
.nav-link {
  margin-left: 10px;
}
.Brand {
  cursor: pointer;
  
  
}
.App {
  text-align: center;
  letter-spacing: -0.8px;
  height: 100%;
  

  
}


#me {
  max-width: 1400px;
  min-width: 1200px;
  margin: 0 auto;
}
.introduce{
  float: left;
  width: calc(100%/3);
  margin-bottom: 50px;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.col-md-4{
 
  /*다크 모드 때 변화*/

  margin: 0px;
}
thead {
  border-bottom: black 2px solid;
}
#Table-title{

  padding-top: 100px;
